<template lang="html">
  <div id="recommend">
    <div id="banner" class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="banner of banners">
          <a :href="banner.href">
            <img :src="banner.imgUrl">
          </a>
        </div>
      </div>
      <div class="swiper-pagination"></div>
    </div>

    <div id="rcmMenu">
      <a href="javascript:;">
        <i class="iconfont icon-shouyinji01"></i>
        <p>私人FM</p>
      </a>
      <a href="javascript:;">
        <i class="iconfont icon-icon-test17"></i>
        <p>每月歌曲推荐</p>
      </a>
      <a href="javascript:;">
        <i class="iconfont icon-paixingbang2"></i>
        <p>云音乐热歌榜</p>
      </a>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
import Swiper from 'swiper'
export default {
  name: 'recommend',
  data() {
    return {
      banners: ''
    }
  },
  created: function() {
    if (!this.banners) {
      axios.get('../../../../static/data.json')
        .then((res) => {
          this.banners = res.data.banners;
          setTimeout(function() {
            new Swiper("#banner", {
              autoplay: 3000,
              loop: true,
              autoplayDisableOnInteraction: false,
              // 如果需要分页器
              pagination: '.swiper-pagination',
              paginationClickable: true,
              observer: true,
              observeParents: true
            })
          }, 50);

        })
        .catch((err) => {
          console.log(err)
        })
    }
  }

}
</script>

<style lang="css">

#banner img{
  width: 100%;
}

#rcmMenu{
  display: flex;
  width: 100%;
  height: 2.986111111111111rem;
  justify-content: space-between;
  box-sizing: border-box;
  border-bottom: 1px solid #dde1e0;
}

#rcmMenu a{
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
  text-align: center;
}

#rcmMenu a i{
  display: inline-flex;
  width: 1.4444444444444444rem;
  height: 1.4444444444444444rem;
  justify-content: center;
  align-items: center;
  font-size: 0.6944444444444444rem;
  color: #c83838;
  box-sizing: border-box;
  border: 1px solid #c83838;
  border-radius: 50%;
  margin-top: 0.5555555555555556rem;
  margin-bottom: 0.2777777777777778rem;
}
#rcmMenu a p{
  color: #303030;
  font-size: 0.2777777777777778rem;
}
</style>



